<template>
    <div class="info" v-loading="loading">
        <div class="content">
            <el-form ref="ruleForm" :model="ruleForm" label-width="100px" class="rule-form">
                <el-form-item :label="$L('会员头像')+'：'">
                    <user-avatars  class="img" :src="avator"></user-avatars>
                </el-form-item>
                <el-form-item :label="$L('会员账号')+'：'" prop="user">
                    <el-input size="small" :disabled="true" v-model="ruleForm.username"></el-input>
                </el-form-item>
                <el-form-item :label="$L('昵称')+'：'" prop="name" v-if="ruleForm.hasOwnProperty('nickname') && ruleForm.nickname.hasOwnProperty('isset') && ruleForm.nickname.isset >= 0">
                    <el-input size="small" v-model="ruleForm.nickname.val"></el-input>
                </el-form-item>
                <el-form-item :label="$L('邮箱')+'：'" prop="email" v-if="ruleForm.hasOwnProperty('email') && ruleForm.email.hasOwnProperty('isset') && ruleForm.email.isset >= 0" >
                    <el-input size="small" v-model="ruleForm.email.val" :disabled="ruleForm.validEmail.isset == 1? true : false"></el-input>
                    <span v-if="ruleForm.validEmail.isset == 1" class="font" @click="modifyEmail">{{ $L('修改') }}</span>
                </el-form-item>
                <el-form-item :label="$L('手机号')+'：'" prop="mobile" v-if="ruleForm.hasOwnProperty('mobile') && ruleForm.mobile.hasOwnProperty('isset') && ruleForm.mobile.isset >= 0">
                    <el-input size="small" v-model="ruleForm.mobile.val" :disabled="ruleForm.validPhone.isset == 1? true : false"></el-input>
                    <span v-if="ruleForm.validPhone.isset == 1" class="font" @click="modifyMobile">{{ $L('修改') }}</span>
                </el-form-item>
                <el-form-item :label="$L('姓名')+'：'" prop="name" v-if="ruleForm.hasOwnProperty('name') && ruleForm.name.hasOwnProperty('isset') && ruleForm.name.isset >= 0">
                    <el-input size="small" v-model="ruleForm.name.val"></el-input>
                </el-form-item>
                <el-form-item :label="$L('电话')+'：'" prop="tel" v-if="ruleForm.hasOwnProperty('tel') && ruleForm.tel.hasOwnProperty('isset') && ruleForm.tel.isset >= 0">
                    <el-input size="small" v-model="ruleForm.tel.val"></el-input>
                </el-form-item>

                <el-form-item :label="$L('地址')+'：'" prop="regions" v-if="ruleForm.hasOwnProperty('regions') && ruleForm.regions.hasOwnProperty('isset') && ruleForm.regions.isset >= 0">
                    <region-view style="width: 280px" v-model="ruleForm.regions.val" :placeholder="ruleForm.regions.defaultCity"></region-view>
                </el-form-item>
                <el-form-item :label="$L('详细地址')+'：'" prop="address" v-if="ruleForm.hasOwnProperty('address') && ruleForm.address.hasOwnProperty('isset') && ruleForm.address.isset >= 0">
                    <el-input size="small" v-model="ruleForm.address.val"></el-input>
                </el-form-item>
                <el-form-item :label="$L('邮编')+'：'" prop="pcode" v-if="ruleForm.hasOwnProperty('pcode') && ruleForm.pcode.hasOwnProperty('isset') && ruleForm.pcode.isset >= 0">
                    <el-input size="small" v-model="ruleForm.pcode.val"></el-input>
                </el-form-item>
                <el-form-item :label="$L('公司名称')+'：'" prop="org" v-if="ruleForm.hasOwnProperty('org') && ruleForm.org.hasOwnProperty('isset') && ruleForm.org.isset >= 0">
                    <el-input size="small" v-model="ruleForm.org.val"></el-input>
                </el-form-item>
                <el-form-item :label="$L('联系人')+'：'" prop="contactor" v-if="ruleForm.hasOwnProperty('contactor') && ruleForm.contactor.hasOwnProperty('isset') && ruleForm.contactor.isset >= 0">
                    <el-input size="small" v-model="ruleForm.contactor.val"></el-input>
                </el-form-item>


                <el-form-item v-for="(item,index) in ruleForm.getDivName" :key="index" :label="item.key"  prop="name1"  v-if="ruleForm.hasOwnProperty('getDivName')">
                    <el-input size="small" v-model="item.val"></el-input>
                </el-form-item>

                <el-form-item class="bottom">
                    <el-button class="btn" size="small" @click="submitForm('ruleForm')">{{ $L('重置') }}</el-button>
                    <el-button class="btn" type="primary" size="small" @click="resetForm('ruleForm')">{{ $L('保存') }}</el-button>
                </el-form-item>
            </el-form>
            <div class="head">
                <div class="box">
                    <p class="tit">{{ $L('选择头像') }}</p>
                    <user-avatars class="my-img" :src="avator"></user-avatars>
                    <label class="label">
                        <input class="mainBgColor" type="button" id="btn" :value="$L('选择头像')"/>
                        <input id="fileinp" type="file" name="CommentsPic" :placeholder="$L('上传图片')" multiple accept="image/png,image/jpeg,image/jpg" @change="upImgFunc"/>
                    </label>

                    <div class="tips">{{ $L('仅支持JPG、GIF、PNG格式，文件小于2M') }}</div>
                </div>
                <div class="box">
                    <p class="tit">{{ $L('系统头像') }}</p>
                    <ul class="img-group">
                        <li class="img-li" v-for="(item,index) in imgUl" :key="index"><img class="img" :src="item.img" alt="" @click="selectAvator(item.img)"></li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import bindEmail from "../../../components/account/bindEmail.vue"
    import bindPhone from "../../../components/account/bindPhone.vue"
    import regionView from "../../../components/common/regionView";
    import userAvatars from "../../../components/common/userAvatars";
    export default {
        name: "info",
        inject: ["reload"],
        components :{
            regionView,
            bindEmail,bindPhone,userAvatars
        },
        data (){
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                imageUrl: '',
                ruleForm:{
                    username:'',
                    name:'',
                    tel:'',
                    email:'',
                    mobile:'',
                    address:'',
                    regions:[],
                },
                defaultCity:'',
                fileList: [],
                emailValid: false,
                mobileValid: false,
                imgUl:[],
                avator:require('../../../assets/img/info/1.jpg'),
                uploadImg:[],
                encryptEmail:'',
                encryptMobile:'',
                arr: [],
                localHead:'',

            }
        },
        created (){
            this.getData();
        },
        methods: {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('userInfo', {} , function (reData) {
                    if (reData.flag === 1) {
                        _self.ruleForm = reData.data;
                        _self.avator = reData.data.head;
                        _self.imgUl = reData.data.localHeadImg;
                        //这里为什么会获取邮箱和手机号码呢?主要是用户判断用户有没有更改邮箱或手机号码
                        if (reData.data.hasOwnProperty("email")) {
                            _self.encryptEmail = reData.data.email.val;
                        }
                        if (reData.data.hasOwnProperty("mobile")) {
                            _self.encryptMobile = reData.data.mobile.val;
                        }
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            //上传图片
            upImgFunc(event) {
                const _self = this;
                let files = event.target.files[0];
                let size = Math.floor(files.size / 1024);
                let type = files.type.substring(6);
                if (size > 2*1024*1024) {
                    _self.$Message.info('请选择2M以内的图片！');
                    return false;
                }
                if(!(type === 'png' || type === 'jpeg' || type === 'jpg')){
                    _self.$Message.info('请选择png、jpeg、jpg的图片格式！');
                    return false;
                }
                _self.uploadImg = {
                    'file':files
                };
                let reader = new FileReader();
                reader.readAsDataURL(files);
                reader.onload = function(evt){
                    _self.avator = evt.target.result;
                }
            },
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        // alert('submit!');
                        this.getData();
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm() {
                //好吧,现在我们来开始进行必填项与非必填项的判断吧
                console.log(this.avator);
                let arr = this.argVarName();
                for (let v in this.ruleForm) {
                    if (this.in_array_key(v, arr) && this.ruleForm[v].isset==1 && !this.ruleForm[v].val) {
                        this.$message.error(this.$L(arr[v]+"为必填项"));
                        return;
                    }
                }


                //现在来处理自定义选项的判断
                if (this.ruleForm.hasOwnProperty("getDivName")) {

                    for(let v in this.ruleForm.getDivName){
                        if (this.ruleForm.getDivName[v].isRequired == 1 && !this.ruleForm.getDivName[v].val.trim()) {
                            this.$message.error(this.$L(this.ruleForm.getDivName[v].key+"是必填项"));
                            return;
                        }
                    }
                }
                //有两个值比较特殊,分别是邮箱和手机号码,对他们的值进行特殊处理
                if (this.ruleForm.hasOwnProperty("email") && this.ruleForm.email.val.trim() != this.encryptEmail && this.ruleForm.email.val.indexOf("*") == -1) {
                    this.ruleForm.email.realVal = this.ruleForm.email.val;
                }
                if (this.ruleForm.hasOwnProperty("mobile") && this.ruleForm.mobile.val.trim() != this.encryptMobile && this.ruleForm.mobile.val.indexOf("*") == -1) {
                    this.ruleForm.mobile.realVal = this.ruleForm.mobile.val;
                }
                const _self = this;
                _self.loading = true;
                let formData = {
                    formData: _self.ruleForm,
                    headImg : _self.uploadImg ? _self.uploadImg : _self.avator,
                    localHead : _self.localHead,
                };
                let postData = _self.$objToFormData(formData);
                _self.$api('saveUserInfo', postData , function (reData) {
                    if (reData.flag === 1) {
                        _self.$message({
                            type: "success",
                            message: _self.$L("保存成功!")
                        });
                        _self.reload();
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {

                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.resetForm();
                            _self.loading = false;
                        }).catch(action => {
                            _self.loading = false;
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.resetForm();
                        _self.loading = false;
                    }).catch(action => {
                        _self.loading = false;
                    });
                }, true);

            },
            in_array_key(search,array) {
                for(let i in array){
                    if(i==search){
                        return true;
                    }
                }
                return false;
            },
            argVarName(){
                let array = [];
                array['email'] = "邮件";
                array['nickname'] = "昵称";
                array['mobile'] = "手机";
                array['tel'] = "电话";
                array['address'] = "地址";
                array['name'] = "姓名";
                array['pcode'] = "邮政编码";
                array['org'] = "单位";
                array['contactor'] = "联系人";
                array['regions'] = "通用地区";
                return array;
            },
            modifyEmail(){

                this.$store.commit('setDialogObj', {
                    name: this.$L('安全验证'),
                    view: bindEmail,
                    width: '500px',
                    data: {
                        isset:this.ruleForm.hasOwnProperty('validEmail') && this.ruleForm.validEmail.hasOwnProperty('isset') ? parseInt(this.ruleForm.validEmail.isset) : 0,
                        emailNum:this.ruleForm.email.val,
                        issetMobile:this.ruleForm.hasOwnProperty('validPhone') && this.ruleForm.validPhone.hasOwnProperty('isset') ? parseInt(this.ruleForm.validPhone.isset) : 0,
                        mobileNum:this.ruleForm.mobile.val,
                        trueMobileNum: this.ruleForm.mobile.realVal,
                    },
                });
            },
            modifyMobile(){
                this.$store.commit('setDialogObj', {
                    name: this.$L('安全验证'),
                    view: bindPhone,
                    width: '500px',
                    data: {
                        isset:this.ruleForm.hasOwnProperty('validPhone') && this.ruleForm.validPhone.hasOwnProperty('isset') ? parseInt(this.ruleForm.validPhone.isset) : 0,
                        mobileNum:this.ruleForm.mobile.val,
                        trueMobileNum: this.ruleForm.mobile.realVal,
                    }
                });
            },
            selectAvator(img){
                this.avator = img;
                this.localHead = img;
                this.uploadImg = '';
            }

        }

    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib";
    .info{
        padding: 20px!important;
        background: #fff;
        width: auto!important;
        margin: 20px;
        .content{
            overflow: hidden;
            padding: 20px 20px 80px;
            width: 1100px;
            .rule-form{
                float: left;
                width: 380px;
                .el-form-item{
                    margin-bottom: 5px;
                    position: relative;
                    .font{
                        position: absolute;
                        width: 30px;
                        top: 13px;
                        right: -40px;
                        color: #1D76F6;
                        cursor: pointer;
                        line-height: 1;
                    }
                }
                .el-input__inner{
                    padding: 0 10px;
                }
                .address{
                    .el-select{
                        width: 90px;
                        margin-right: 5px;
                        & .el-select:last-child{
                            margin-right: 0;
                        }
                    }
                    .el-select:last-child{
                        margin-right: 0;
                    }
                }
                .bottom{
                    .btn{
                        width: 120px;
                    }
                }
                .img{
                    width: 60px;
                    height: 60px;
                    border-radius: 50%;
                    /deep/ .img-error {
                        width: 100%;
                        height: 100%;
                        border-radius: 50%;
                    }
                    /deep/ .el-image__inner{
                        border-radius: 50%;
                    }
                }
            }
            .head{
                float: right;
                display: inline-block;
                width: 500px;
                border: 1px solid #ccc;
                padding: 30px 50px 0;
                .my-img{
                    height: 135px;
                    width: 135px;
                    margin: 0 auto;
                }
                .label{
                    position: relative;
                    display: block;
                    cursor: pointer;
                    margin: 10px auto;
                    .mainBgColor{
                        color: #fff !important;
                    }
                    #fileinp{
                        position: absolute;
                        left: 50%;
                        opacity: 0;
                        width: 90px;
                        height: 32px;
                        cursor: pointer;
                        transform: translateX(-50%);
                    }
                    #btn{
                        display: inline-block;
                        line-height: 1;
                        white-space: nowrap;
                        cursor: pointer;
                        background: #FFFFFF;
                        border: 1px solid #DCDFE6;
                        border-color: #DCDFE6;
                        color: #606266;
                        -webkit-appearance: none;
                        text-align: center;
                        -webkit-box-sizing: border-box;
                        box-sizing: border-box;
                        outline: none;
                        margin: 0;
                        -webkit-transition: .1s;
                        transition: .1s;
                        font-weight: 500;
                        -moz-user-select: none;
                        -webkit-user-select: none;
                        -ms-user-select: none;
                        padding: 9px 20px;
                        font-size: 12px;
                        border-radius: 0px;
                    }
                }
                .box{
                    text-align: center;
                    margin-bottom: 50px;
                    .upload{
                        display: block;
                        margin: 5px auto 10px;
                        width: 120px;
                    }
                    .tit{
                        text-align: left;
                        font-size: 14px;
                        margin-bottom: 20px;
                    }
                    .img-group{
                        display: flex;
                        justify-content: space-between;
                        flex-wrap: wrap;
                        .img-li{
                            height: 86px;
                            width: 86px;
                            padding: 2px;
                            border: 1px solid #ccc;
                            cursor: pointer;
                            margin-bottom: 20px;
                            .img{
                                height: 80px;
                                width: 80px;
                            }
                        }
                    }
                }

            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>
